export default {
    data(){
      return{
          flag:1,
        searchResult: [],
        parms:{
          buildingname:null,
          floor:null,
          area:null,
          price:null,
          name:null,
          phone:null,
          rors:1
        }
      }
    },
    methods:{
      checked(item){
        this.flag=item
        this.parms.rors=item
      },
      submit(){
        let formdata=new FormData()
        for (let k in this.parms){
          formdata.append(k,this.parms[k])
        }
        this.$axios.post("/ower/add",formdata).then((res)=>{
          if (res.data.code=='ok'){
            Toast.success('放盘成功！');
            this.parms={
              buildingname:null,
              floor:null,
              area:null,
              price:null,
              name:null,
              phone:null,
              rors:1
            }
          } else {
            Toast.fail('放盘失败！');
          }
        })
      },
      onSave() {
        Toast('save');
      },
      onDelete() {
        Toast('delete');
      },
      onChangeDetail(val) {
        if (val) {
          this.searchResult = [{
            name: '黄龙万科中心',
            address: '杭州市西湖区'
          }];
        } else {
          this.searchResult = [];
        }
      }
    }
}



